






















import Vue from 'vue'
import TheNavigation from "./TheNavigation.vue"

export default Vue.extend({
    name: "AppBar",
    data: () => ({
        title:'Home'
    }),
    components: {
        TheNavigation
    },
    mounted() {
        this.setData()
    },
    watch: {
        $route() {
            this.setData()
        }
    },
    methods: {
        drawer() {
            this.$root.$refs.navigationDrawer.open();
        },
        setData() {
            this.title = this.$route.meta(this.$route).title
        }
    }
})
